<template>
  <v-form>
    <v-progress-linear
        v-show="loading"
        v-model="valueDeterminate"
        class="customProgress"
      >
    </v-progress-linear>
    <v-container ma-auto pa-4>
      <h1>查课表</h1>
        <v-layout row wrap >
          <v-flex xs12 sm6 md3>
            <v-text-field 
              label="输入学号"
              hint="格式如 161810112"
              clearable
              v-model="studentId"
              prepend-icon="search"
            >
            </v-text-field>
          </v-flex>
          <v-flex xs12 sm6 md3>
            <v-text-field
              v-model="password"
              name="password"
              label="密码"
              hint="教务处官网密码"
              counter
              type="password"
              prepend-icon="lock"
          ></v-text-field>
          </v-flex>
        </v-layout>
        <v-layout row wrap >
          <v-flex xs12 sm6 md3 >
            <v-text-field
              hint="格式为 2018-2019"
              clearable
              label="学年年份"
              v-model="semester_year"
            >
            </v-text-field>
          </v-flex>
          <v-flex xs12 sm6 md1 d-flex>
            <v-select
              v-model="semester"
              :items="semesters"
              label="学期"
            ></v-select>
          </v-flex>
          <v-flex xs12 sm6 md3 >
            <v-text-field
              hint="格式为 2019-09-02"
              label="开学日期"
              v-model="semester_start_date"
              clearable
            >
            </v-text-field>
          </v-flex>
        </v-layout>
        <v-layout fill-height row align-center>

          <v-btn
            :loading="loading"
            :disabled="loading"
            color="secondary"
            key="IdQuery"
            @click="doIdQuery()">
            查询
          </v-btn>
          <v-btn 
            flat 
            outline
            :loading="loading"
            :disabled="loading"
            @click="exportICS()"
          >
              导出课表
              <v-icon right dark>cloud_download</v-icon>
          </v-btn>

        </v-layout>
      <weekly 
        v-bind:calendarEvent="getLessons()" 
        v-bind:setToday="getStartDate()" 
        v-bind:showSelection="getShowSelection()"
        v-bind:setMemberData="getMemberData()"/>
    </v-container>
    <v-snackbar
      v-model="snackbar"
      color="error"
      top
    >
    发生错误 请重试
    <v-spacer />
    <v-btn
      flat
      icon
      @click="snackbar = false"
    >
      <v-icon>close</v-icon>
    </v-btn>
    </v-snackbar>
  </v-form>
</template>

<style scoped>
.customProgress {
  position: fixed;
  margin: 0;
  height: 4px;
}
</style>

<script>
import weekly from './components/weekly';
import axios from 'axios';
import { saveAs } from 'file-saver';

export default {
  name: 'FreeTime',
  components: {
    weekly,
  },
  meta: {title: '个人课程表'},
  data () {
    return {
      drawer: null,
      radios: 'byId',
      studentId: '',
      
      semesters: [
        1,
        2
      ],
      semester_year: this.getDefaultSemesterYear(),
      semester: this.getDefaultSemester(),
      semester_start_date: "2019-09-02",
      callback_data: null,
      calendar: [],
      startDate: '',
      loading: false,
      snackbar: false,
      valueDeterminate: 0,
      tmpString: "",
      export_loading: false,
      password: "",
      icsData: ""
    }
  },
  methods: {
    getDefaultSemesterYear () {
      var today = new Date();
      var month = today.getMonth() + 1;
      var yyyy = today.getFullYear() - 1;
      if(month >= 8){
        yyyy += 1;
      }
      var yyyy_2 = yyyy + 1;
      return (yyyy.toString() + "-" + yyyy_2.toString());
    },
    getDefaultSemester () {
      var today = new Date();
      var month = today.getMonth() + 1;
      if(month >= 8){
        return 1;
      }
      return 2;
    },
    getLessons (){
      if(this.callback_data === null) {
        return [];
      }
      return this.callback_data.lesson;
    },
    getStartDate (){
      if(this.callback_data === null) {
        return "2019-01-18";
      } 
      return this.callback_data.start_date;
    },    
    doIdQuery (){
      this.valueDeterminate = 0;
      this.loading = true;
      this.tmpString = (/^[0-9]{4}-[0-9]{2}-[0-9]{2}$/.test(this.semester_start_date) ? ("?start_date=" + this.semester_start_date) : "");

      axios({
        url: 'https://huk3spc54a.execute-api.ap-northeast-1.amazonaws.com/b/api/get_schedule',
        method: 'GET',
        params: {
          studentId: this.studentId,
          password: this.password,
          semester_year: this.semester_year,
          semester: this.semester,
          start_date: this.semester_start_date
        },
      })
      .then(response => (this.callback_data = response.data.data))
      .catch(error => this.showError());
    },
    exportICS (){
      this.loading = true;
      axios({
        url: 'https://huk3spc54a.execute-api.ap-northeast-1.amazonaws.com/b/api/download_ics',
        method: 'GET',
        responseType: 'blob',
        params: {
          studentId: this.studentId,
          password: this.password,
          semester_year: this.semester_year,
          semester: this.semester,
          start_date: this.semester_start_date
        },
      }).then(response => (this.icsData = response.data))
      .catch(error => this.showError());
    },
    getShowSelection (){
      return false;
    },
    getMemberData (){
      return null;
    },
    showError (){
      this.snackbar = true;
      this.loading = false;
    }
  },
  props: {
    source: String
  },
  watch: {
    callback_data: function() {
      this.loading = false
    },
    icsData: function(){
      this.loading = false;
      saveAs(new Blob([this.icsData]), 'lesson.ics');
    }
  }
}
</script>
