<template>
  <v-layout wrap>
    <v-flex
      xs12
      class="mb-3"     
    >
      <v-toolbar>
        <v-toolbar-title class="black--text">{{ getDateString() }}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-title class="black--text">{{ currentDate }}</v-toolbar-title>
      </v-toolbar>
      <v-sheet height="400" elevation-3>
        <!-- now is normally calculated by itself, but to keep the calendar in this date range to view events -->
        <v-calendar
          ref="calendar"
          v-model="start"
          :now="today"
          :value="today"
          :end="end"
          color="primary"
          type="week"
          :weekdays="weekdays"
          interval-minutes="120"
          first-interval="3"
          interval-count="8"
          @change="onDateChange"
        >
          <!-- the events at the top (all-day) -->
          <template v-slot:dayHeader="{ date }">
            <template v-for="event in eventsMap[date]">
              <!-- all day events don't have time -->
              <div
                v-if="!event.time"
                :key="event.title"
                class="my-event"
                @click="open(event)"
                v-html="event.title"
              ></div>
            </template>
          </template>
          <!-- the events at the bottom (timed) -->
          <template v-slot:dayBody="{ date, timeToY, minutesToPixels }">
            <template v-for="event in eventsMap[date]">
              <!-- timed events -->
              <div
                v-if="event.time"
                :key="event.title"
                :style="{ top: timeToY(event.time) + 'px', height: minutesToPixels(event.duration) + 'px' }"
                class="my-event with-time"
                @click="open(event)"
                v-html="event.title"
              ></div>
            </template>
          </template>
        </v-calendar>
      </v-sheet>
    </v-flex>
    <v-flex
      xs12
      class="hidden-sm-and-up text-xs-center"
      v-if="selection && memberData !== null"
    >
      <v-select
        mx-auto
        v-model="selectedMember"
        :items="memberLessonData"
        item-text="full_name"
        item-value="sid"
        return-object
        label="人员"
        class="text-truncate"
      ></v-select>
    </v-flex>
    <v-flex
      xs6
      sm4
      class="text-sm-left text-xs-center"
    >
      <v-btn @click="doCalendarPrev()">
        <v-icon
          dark
          left
        >
          keyboard_arrow_left
        </v-icon>
        PREV
      </v-btn>
    </v-flex>
    <v-flex
      sm4
      class="hidden-xs-only text-xs-center"
      v-if="selection && memberData !== null"
    >
      <v-select
        mx-auto
        v-model="selectedMember"
        :items="memberLessonData"
        item-text="full_name"
        item-value="sid"
        return-object
        label="人员"
        class="text-truncate"
      ></v-select>
    </v-flex>
    <v-flex
      class="hidden-xs-only"
      v-else
    >
      <v-spacer />
    </v-flex>
    <v-flex
      xs6
      sm4
      class="text-sm-right text-xs-center"
    >
      <v-btn @click="doCalendarNext()">
        <v-icon
          right
          dark
        >
          keyboard_arrow_right
        </v-icon>
        NEXT
      </v-btn>
    </v-flex>
  </v-layout>
</template>

<script>
  export default {
    name: "weekly",
    props: {
      calendarEvent: {
        type: Array
      },
      setToday: {
        type: String
      },
      setSemester: {
        type: Number
      },
      setSemesterYear: {
        type: String
      },
      showSelection: {
        type: Boolean
      },
      setMemberData: {
        type: Object
      }
    },
    
    data: () => ({
      today: '2019-01-08',
      start: '2019-01-08',
      end: '2019-01-15',
      semester: null,
      semester_year: null,
      week_num: null,
      weekdays: [1,2,3,4,5,6,0],
      currentDate: "",
      events: [
        {
          title: 'Weekly Meeting',
          date: '2019-01-07',
          time: '09:00',
          duration: 45
        },
        {
          title: 'Thomas\' Birthday',
          date: '2019-01-10'
        },
        {
          title: 'Mash Potatoes',
          date: '2019-01-07',
          time: '09:30',
          duration: 180
        }
      ],
      memberData: null,
      selectedMember: null,
      selection: false,
      memberLessonData: null
    }),
    computed: {
      // convert the list of events into a map of lists keyed by date
      eventsMap () {
        const map = {}
        this.events.forEach(e => (map[e.date] = map[e.date] || []).push(e))
        return map
      }
    },
    mounted () {
      this.$refs.calendar.scrollToTime('08:00')
    },
    methods: {
      open (event) {
        alert(event.title)
      },
      onDateChange (event) {
        this.currentDate = event.start.date
      },
      getDateString () {
        if(this.week_num === null){
          return "";
        }
        return "第" + this.week_num.toString() + "周";
      },
      doCalendarPrev (event) {
        if(this.week_num <= 1){
          return;
        }
        this.$refs.calendar.prev();
        this.week_num -= 1;
      },
      doCalendarNext (event) {
        if(this.week_num >= 20){
          return;
        }
        this.$refs.calendar.next();
        this.week_num += 1;
      },
      getFullName (sid, name){
        return sid.toString() + name;
      }
    },
    watch: {
      calendarEvent: function() {
        this.events = this.calendarEvent
      },
      setToday: function() {
        this.today = this.setToday
        this.start = this.setToday
        this.week_num = 1;
      },
      setSemester: function() {
        this.semester = this.setSemester
      },
      setSemesterYear: function() {
        this.semester_year = this.setSemesterYear
      },
      setMemberData: function(){
        if(this.setMemberData){
          this.memberData = this.setMemberData;
          this.selectedMember = this.memberData.lesson[0];
          this.memberLessonData = this.memberData.lesson;
        }
      },
      showSelection: function(){
        this.selection = this.showSelection
      },
      selectedMember: function(){
        this.events = this.selectedMember.data
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .my-event {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    border-radius: 2px;
    background-color: #1867c0;
    color: #ffffff;
    border: 1px solid #1867c0;
    font-size: 12px;
    padding: 3px;
    cursor: pointer;
    margin-bottom: 1px;
    left: 4px;
    margin-right: 8px;
    position: relative;

    &.with-time {
      position: absolute;
      right: 4px;
      margin-right: 0px;
    }
  }
</style>
